<script>
  const formatter = new Intl.NumberFormat("en-US", {
    style: "currency",
    currency: "USD"
  });
  let loanAmount = 10000;
  let interestRateInput = 1499;
  let monthlyPaymentInput = 500;

  $: months = (loanAmount / monthlyPaymentInput).toFixed(1);
  $: interestRate = interestRateInput / 100;
  $: totalPayments = months;
  $: monthlyInterestRate = interestRate / 100 / 12;
  $: monthlyPayment =
    (loanAmount *
      Math.pow(1 + monthlyInterestRate, totalPayments) *
      monthlyInterestRate) /
    (Math.pow(1 + monthlyInterestRate, totalPayments) - 1);

  $: totalPaid = monthlyPayment * totalPayments;
  $: interestPaid = totalPaid - loanAmount;
</script>

<style>
  h1 {
    text-align: center;
  }
  .outputs {
    display: flex;
    justify-content: space-between;
    background: #0073ff;
    margin-top: 15px;
    padding: 0.5em;
    border-radius: 4px;
    font-size: 20px;
    text-align: center;
    color: white;
  }
  .outputs.settings {
    background-color: darkorange;
    justify-content: center;
  }
  .outputs p {
    width: 50%;
    margin-bottom: 0;
  }
  .controls {
    margin-top: 1em;
  }
</style>

<main class="container">
  <div class="row">
    <h1>Credit Cardulator</h1>
  </div>
  <div class="row">
    <label>Loan Amount</label>
    <input
      class="u-full-width"
      min="1"
      type="number"
      bind:value={loanAmount}
      placeholder="Enter loan amount..." />
  </div>

  <div class="row">
    <div class="columns six outputs settings">{months} months</div>
    <div class="columns six outputs settings">{interestRate.toFixed(2)} %</div>
  </div>

  <div class="row outputs">
    <p>Payments:</p>
    <p>{formatter.format(monthlyPaymentInput)}</p>
  </div>
  <div class="row outputs">
    <p>Total Paid:</p>
    <p>{formatter.format(totalPaid)}</p>
  </div>
  <div class="row outputs">
    <p>Interest Paid:</p>
    <p>{formatter.format(interestPaid)}</p>
  </div>

  <div class="row controls">

    <div class="columns four">
      <label>Monthly Payments</label>
      <input
        type="range"
        class="u-full-width years"
        min="1"
        max={loanAmount}
        bind:value={monthlyPaymentInput} />
    </div>

    <div class="columns four">
      <label>Months</label>
      <input
        type="range"
        class="u-full-width years"
        min="1"
        max="50"
        bind:value={months} />
    </div>

    <div class="columns four">
      <label>Interest Rate</label>
      <input
        type="range"
        class="u-full-width years"
        min="1"
        max="3000"
        bind:value={interestRateInput} />
    </div>

  </div>

</main>
